<template>
	<div class="parking-manage">
		<h3></h3>
		<el-form ref="parkingform" :model="parkingform" >
			<el-form-item >
				<label></label>
				<el-input v-model="parkingform.lotId" placeholder="" ></el-input>
			</el-form-item>
			<el-form-item>
				<label></label>
				<el-input v-model="parkingform.cardId" placeholder=""></el-input>
			</el-form-item>
			<el-form-item>
				<el-button class="parking-button" type="primary" @click="parkingCar()"></el-button>
				<el-button class="parking-button" type="primary" @click="withdrawCar()"></el-button>
			</el-form-item>
		</el-form>	
		<!-- <h3></h3>
		<el-form ref="parkingform" :model="parkingform" >
            <el-form-item>
				<label></label>
				<el-input v-model="withdrawform.cardId" placeholder=""></el-input>
			</el-form-item>
			<el-form-item>
				<el-button class="parking-button" type="primary" @click="parkingCar()"></el-button>
			</el-form-item>
		</el-form> -->
	</div>
</template>

<script>
	export default{
		name: 'ParkingManage',
		mounted(){
			this.$nextTick(function () {
				this.spaceLimit();
			})
		},
		data(){
			return{
				//
				parkingform: {
					lotId: ``,
                    cardId: ``
				},
			}
		},
		methods:{
			spaceLimit(){
				var input=document.getElementsByTagName("input");
				for(var i=0;i<input.length;i++){
					input[i].onkeyup=function(){
						this.value=this.value.replace(/^ +| +$/g,'');
					}
				}
			},
			//
			parkingCar(){
				if(this.parkingform.lotId && this.parkingform.cardId){
					this.axios.post(this.baseURI + "/parkingRecord/insertParkingRecord",{
						parkingNum: this.parkingform.lotId,
						cardNum: this.parkingform.cardId
					}).then(res => {
						console.log(res);
						if(res.data.message == "OK"){
							this.$notify({
							title: '',
							message: '',
							type: 'success'
						});
						}else{
							this.$notify({
								title: '',
								message: res.data.message,
								type: 'error'
							});
						}
					})
				}
				else{
					this.$notify({
						title: '',
						message: '',
						type: 'error'
					});
				}
				
			},
			//
			withdrawCar(){
				if(this.parkingform.lotId && this.parkingform.cardId){
					this.axios.post(this.baseURI + "/parkingRecord/updateParkingRecord",{
						parkingNum: this.parkingform.lotId,
						cardNum: this.parkingform.cardId
					}).then(res => {
						console.log(res);
                        if(res.data.message == "OK"){
                        	this.$notify({
                        		title: '',
                        		message: '',
                        		type: 'success'
                        	});
						}else{
							this.$notify({
								title: '',
								message: res.data.message,
								type: 'error'
							});
						}
					})
				}
				else{
					this.$notify({
						title: '',
						message: '',
						type: 'error'
					});
				}
			}
	    }
    }
</script>

<style scoped>
	.parking-manage{
        overflow: hidden;
	}
	.el-button{
		margin-left: 100px;
		width: 100px;
	}
	.el-input{
		margin-left: 100px;
		width: 300px;
	}
	.el-form-item{
		min-width: 600px;
	}
	label{
		width: 100px;
		position: absolute;
	}
	h3{
		margin-top: 10px;
		margin-bottom: 35px;
	}
</style>


