<template>
	<div class="user-bill">
		<div class="user-bill-tit"></div>
    <div class="user-bill-detail">
      <div><span></span>{{$route.params.billNum}}</div>
      <div><span></span>{{billData.cardNum}}</div>
      <div><span></span>{{billData.parkingName}}</div>
      <div><span></span>{{billData.price}}/</div>
      <div><span></span>{{billData.account}}</div>
      <div><span></span>{{secondToDate(billData.firstDate) + "~" + secondToDate(billData.statementDate)}}</div>
      <div><span></span>{{secondToDate(billData.statementDate)}}</div>
      <el-button type="primary" v-if="billData.flag == 0 || billData.flag == 3" @click="payforBill()">{{payArr[0]}}</el-button>
      <el-button type="primary" v-else disabled>{{payArr[billData.flag]}}</el-button>
      <router-link to="/Userindex/UserBill"> </router-link>
    </div>
	</div>
</template>

<script>
	export default{
		name: `UserInfo`,
		data(){
			return{
				billData:{
          // billNum: 20180514111111,
          // cardNum: 88888888,
          // singlePrice: 100,
          // totalPrice: 300,
          // lotName:`A`,
          // billTime:`2018-3-31~2018-6-30`,
          // outBillTime:`2018-6-30`,
          // state: ``
        },
         //
        payArr:[``,``,``],
			}
		},
    mounted: function() {
      this.$nextTick(function () {
        // if(!this.$route.params.billNum)
        //   this.$router.push("/Userindex/UserBill");
        this.initBillDetail();
      })
    },
    methods:{
      //
      initBillDetail(){
        this.axios.post(this.baseURI + '/parkingBill/selectParkingBillByBillNum', { "billNum": this.$route.params.billNum})
          .then(res => {
            console.log(res);
            this.billData = res.data.data;
          })
          .catch(err => {
            console.log(err)
          })
      },
      //
      payforBill(){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(() => {
          this.axios.post(this.baseURI + '/parkingBill/payBill',{billNum: this.$route.params.billNum})
          .then( res => {
            if(res.data.message == "OK"){
              this.$message({
                type: 'success',
                message: '!'
              });
              this.initBillDetail();
            }
          })
        })   
            
          
      },
      //
      secondToDate(date){
          let res = new Date(date).toLocaleString();
          return res.slice(0,res.indexOf(' '));
      }
    }
	}
</script>

<style scoped>
	.user-bill{
    min-width: 900px;
	}
	.user-bill-tit{
		font-size: 25px;
		font-weight: bold;
		margin-top: 20px;
    margin-left: 50px;
		margin-bottom: 20px;
	}
  .user-bill-detail{
    margin-left: 50px;
    font-size: 18px;
    line-height: 36px;
  }
  .el-button{
    margin-top: 30px;
  }
  a{
    margin-left: 80px;
    font-size: 14px;
    vertical-align: -10px;
  }
</style>


