<template>
	<div class="price-manage">
		<!--  -->
		<!-- <div class="price-search">
			<el-input v-model="searchNumber" placeholder=""></el-input>
			<el-button type="primary"></el-button>
		</div> -->
		<!--  -->
		<div class="sell-detail">
			<el-table
			:data="sellData"
			style="width: 100%">
		    <el-table-column label="" width="140">
          <template slot-scope="scope">
         		<span>{{scope.row.parkingNum}}</span>
          </template> 
        </el-table-column>
        <el-table-column label="" width="160">
          <template slot-scope="scope">
           	<span>{{scope.row.parkingName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="(/)" width="120">
          <template slot-scope="scope">
           	<span>{{scope.row.price}} </span>
          </template>
        </el-table-column>
        <el-table-column label="(/)" width="120">
          <template slot-scope="scope">
          	<span>{{scope.row.rent}}</span>
          </template>
        </el-table-column>
          <!-- <el-table-column
        prop="benefit"
        label=""
        width="110">
          </el-table-column> -->
         <!--  <el-table-column
        prop="parkingCount"
        label=""
        width="110">
          </el-table-column> -->
        <el-table-column label="" width="170">
          <template slot-scope="scope">
           	<span>{{scope.row.total}}</span>
          </template>
        </el-table-column>
          <!-- <el-table-column
        prop="usePercent"
        label=""
        width="130">
          </el-table-column> -->
        <el-table-column label="">
          <template slot-scope="scope">
          	<el-button
          	size="mini"
          	@click="appearDialog(scope.row.parkingNum,scope.row.price)"></el-button>
          	<el-button
          	size="mini"
          	@click="checkReport(scope.row.parkingName)"></el-button><!-- 
          	<router-link to="/ManagerIndex/SellManage/123">jump</router-link> -->
          </template>
        </el-table-column>
      </el-table>
            <!--  -->
      <el-dialog title="" :visible.sync="priceFormVisible" :lock-scroll="false" >
      	<el-form :disabled="isDisabled">
      		<!-- <el-form-item label="" label-width="150">
      			<el-date-picker
      			v-model="priceform.month"
      			type="month"
      			placeholder=""
      			@change="output()">
      		    </el-date-picker>
      		</el-form-item> -->
      		<el-form-item label="" label-width="180" >
      			<div class="tip" v-show="isDisabled"></div>
      			<el-input v-model.number="modifyPrice"></el-input>
      		</el-form-item>
      	</el-form>
      	<div slot="footer" class="dialog-footer">
      		<el-button @click="priceFormVisible = false"> </el-button>
      		<el-button type="primary" @click="modifyLotPrice()" :disabled="isDisabled"></el-button>
      	</div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
	export default{
		name: `PriceManage`,
		mounted(){
			this.$nextTick(() => {
        this.loadParkingLot();
        this.inputDisabled();
			})
		},
		data(){
			return{
				//
				searchNumber: '',
				// 
				priceFormVisible: false,
				//
				isDisabled:false,
				//Id
				currentLotId: '1',
				//
				modifyPrice: '1000',
				//
				sellData: [/*{
					lotId: '01',
					lotName: '',
					price: '999',
					totalCount: 100
				}, {
					lotId: '02',
					lotName: '',
					price: '999',
					totalCount: 100
				}, {
					lotId: '03',
					lotName: '',
					price: '999',
					totalCount: 100
				}, {
					lotId: '04',
					lotName: '',
					price: '999',
					totalCount: 100
				}*/],
				
				
			}
		},
		methods:{
			//
			inputDisabled(){
	      var date=new Date();
	      var nowDate=date.getMonth()+1+'/'+date.getDate();
	      if(/*nowDate=='3/31'*/nowDate=='5/25'||nowDate=='6/30'||nowDate=='9/30'||nowDate=='12/31'){
	        this.isDisabled=true;
	      }
    	},
			// 
	    loadParkingLot(){
	    	this.axios.get(this.baseURI + `/parkinglot/selectParkinglot`)
	    	.then(res => {
	    		this.sellData = res.data.data;
	    	})
	    },
	    // 
	    appearDialog(lotId,price){
        this.priceFormVisible = true;
        this.currentLotId = lotId;
        this.modifyPrice = price;
	    },
	    // 
	    modifyLotPrice(){
        this.priceFormVisible = false;
        this.inputDisabled();
        if(!this.isDisabled){
        if(typeof(this.modifyPrice) == `number` 
           && this.modifyPrice > 0){
        	this.axios.post(this.baseURI + `/parkinglot/updateParkingLotPrice`,{
        		parkingNum: this.currentLotId,
        		price: this.modifyPrice
        	}).then( res => {
        		console.log(res);
        		if(res.data.message == "OK"){
        			this.$notify({
        				title: '',
        				message: '',
        				type: 'success'
        			});
        		}
        		else{
        			this.$notify({
        				title: '',
        				message: '',
        				type: 'error'
        			});
        		}
        		this.loadParkingLot();
        	})
        }
        else{
        	this.$notify({
        		title: '',
        		message: '',
        		type: 'error'
        	});
        }
        }
        else{
        	this.$notify({
        		title: '',
        		message: '',
        		type: 'error'
        	});
        }
	  	},
    //
    checkReport(lotName){
    	this.$router.push({
    		name: `SellManage`,
    		params:{lotName:lotName}
    	});
    }
	}
}
</script>

<style scoped>
.tip{
  margin-bottom: 20px;
  color: #f00;
}
.price-manage {
   width: 90%;
   min-width: 950px;
}
.price-search {

}
.sell-detail {

}
.el-input {
	display: inline-block;
	width: 200px;
	margin-bottom: 30px;
}
.el-dialog {
}
.el-form{
	margin-left: 60px;
	margin-bottom: -40px;
}

</style>


