<template>
  <div style="width: 980px;" class="admin-paking">
    <el-table
    :data="adminPackingLotTable"
    tooltip-effect="dark"
    highlight-current-row
    @current-change="handleCurrentChange"
    style="width: 100%">
    <el-table-column label="" width="50">
        <template slot-scope="scope"><img v-if="currentLotNum==scope.row.id" src='../../../static/tick.jpg'></template>
      </el-table-column>
    <el-table-column
      type="index"
      label=""
      width="100">
    </el-table-column>
    <el-table-column
      label=""
      width="150">
      <template slot-scope="scope">{{ scope.row.parkingNum }}</template>
    </el-table-column>
    <el-table-column
      prop="parkingName"
      label=""
      width="150">
    </el-table-column>
    <el-table-column
      prop="address"
      label=""
      width="200">
    </el-table-column>
    <el-table-column
      prop="total"
      label=""
      width="150">
    </el-table-column>
    <el-table-column
      prop="rent"
      label="(/)"
      show-overflow-tooltip>
    </el-table-column>
  </el-table>
    <div style="margin-top: 50px; text-align: right;">
      <el-button type="primary" @click="adddialogVisible=true"></el-button>
      <el-button type="primary" @click="deldialogVisible=true"></el-button>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="deldialogVisible" width="30%">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deldialogVisible = false"> </el-button>
        <el-button type="primary" @click="deleteParkingLot()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="adddialogVisible" width="30%">
          <el-input v-model="parkinglot.parkingNum" type="text"/>
          <el-input v-model="parkinglot.parkingName" type="text"/>
          <el-input v-model="parkinglot.address" type="text"/>
          <el-input v-model.trim.number="parkinglot.total" type="text"/>
          <el-input v-model.trim.number="parkinglot.rent" type="text"/>
      <span slot="footer" class="dialog-footer">
        <el-button @click="adddialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParkingLot()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //
      parkinglot: {
          parkingNum: '',
          parkingName: '',
          address: '',
          total: '',
          rent:''
      },
      //
      adminPackingLotTable: [],
      // 
      currentLotNum: ``,
      //
      deldialogVisible: false,
      //
      adddialogVisible: false
    }
  },
  mounted: function() {
    this.$nextTick(function() {
      //
      this.initParkingLot();
    })
  },
  methods: {
    //
    initParkingLot(){
      this.axios.post(this.baseURI +'/parkinglot/selectParkinglot')
        .then(res => {
          this.adminPackingLotTable = [];
          var data = res.data.data;
          for (var i = 0; i < data.length; i++) {
            this.adminPackingLotTable.push(data[i]);
          }
          console.log(this.adminPackingLotTable);
        })
        .catch(err => {
          console.log(err);
        })
    },
    //
    addParkingLot(){
      if (this.parkinglot.parkingNum && this.parkinglot.parkingName && this.parkinglot.address && this.parkinglot.total&& this.parkinglot.rent) {
        if(this.checkForm())
        this.axios.post(this.baseURI + '/parkinglot/insertParkinglot', { "parkingNum": this.parkinglot.parkingNum, "parkingName": this.parkinglot.parkingName, "address": this.parkinglot.address,"total": this.parkinglot.total,"rent": this.parkinglot.rent})
          .then(res => { 
            //
            console.log(res);
            if(res.data.message !== `OK`){
              this.$notify({ 
                title: '',
                message: res.data.message,
                type: 'error'
              });
            }
            this.adddialogVisible = false;
            this.initParkingLot();
          })
          .catch(err => {
            console.log(err)
          })
      }else{ //
          this.$notify({ 
          title: '',
          message: '',
          type: 'error'
        });
        return false;
      }
    },
    //
    deleteParkingLot(){
        //
        if(!this.currentLotNum){
          this.$notify({ 
              title: '',
              message: ``,
              type: 'error'
            });
        }
        this.axios.post(this.baseURI +  `/parkinglot/deleteParkinglot`,{id:this.currentLotNum})
        .then( res => {
          console.log(res);
          if(res.data.state == 1){
            this.$notify({ 
              title: '',
              message: ``,
              type: 'success'
            });
          }
          else{
            this.$notify({ 
              title: '',
              message: res.data.message,
              type: 'error'
            });
          }
          this.currentLotNum = ``;
          this.initParkingLot();
        })
        .catch( err => {
          
        })
        this.deldialogVisible = false;
    },
    // 
    handleCurrentChange(val){
      setTimeout(() =>{
        if(typeof(val.id) !== `undefined`)
        {
          this.currentLotNum = val.id;
          console.log(this.currentLotNum);
        }
      }, 0);
      
    },
    //
    checkForm(){
        // 
        if(typeof(this.parkinglot.total) == `number` && typeof(this.parkinglot.rent) == `number`){
          console.log(this.parkinglot.total + ` ` + typeof(this.parkinglot.rent));
           //
           if(this.parkinglot.total > 0 && this.parkinglot.rent > 0)return true;
        }
          this.$notify({ 
            title: '',
            message: '',
            type: 'error'
          });
          return false;
      }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.el-table {
  width: 100%;
  font-size: 14px;
}
.admin-paking img{
    width: 20px;
    height: 20px;
  }
</style>



