<template>
<div class="search-card">
	<el-input v-model.trim="searchNumber" placeholder=""></el-input>
	<el-button type="primary" @click="searchCard()"></el-button>
	<el-table ref="singleTable"  
	highlight-current-row 
	:data="UserCardTable" 
	tooltip-effect="dark" 
	style="width: 100%" 
	@current-change="handleCurrentChange">
	    <el-table-column label="" width="100">
	    	<template slot-scope="scope"><img v-if="currentCardNum==scope.row.cardNum" src='../../../static/tick.jpg'></template>
	    </el-table-column>
	    <el-table-column label="" width="220">
	    	<template slot-scope="scope">{{ scope.row.cardNum }}</template>
	    </el-table-column>
	    <el-table-column label="" width="220">
	    	<template slot-scope="scope">{{ secondToDate(scope.row.createdTime) }}</template>
	    </el-table-column>
	    <el-table-column label="" show-overflow-tooltip>
			<template slot-scope="scope">{{ cardState[scope.row.state] }}</template>
	    </el-table-column>
	</el-table>
	<!--  -->
	<el-dialog title="" :visible.sync="cardFormVisible" :lock-scroll="false" >
      	<el-form :disabled="isDisabled">
      		<!-- <el-form-item label="" label-width="150">
      			<el-date-picker
      			v-model="priceform.month"
      			type="month"
      			placeholder=""
      			@change="output()">
      		    </el-date-picker>
      		</el-form-item> -->
      		<el-form-item label="" label-width="200" >
      			<span>{{currentCardNum}}</span>
      		</el-form-item>
      		<el-form-item label="" label-width="180" >
      			<!-- <div class="tip" v-show="isDisabled"></div> -->
      			<el-input v-model.number="modifyCardNum" placeholder=""></el-input>
      		</el-form-item>
      	</el-form>
      	<div slot="footer" class="dialog-footer">
      		<el-button @click="cardFormVisible = false"> </el-button>
      		<el-button type="primary" @click="modifyCard()" :disabled="isDisabled"></el-button>
      	</div>
      </el-dialog>
	<!--  -->
	<div class="operator-card">
		<el-button type="primary" @click="judgeChosenCard()"></el-button>
		<el-button type="primary" @click="deleteCard = true"></el-button>
	</div>
</div>
</template>

<script>
	export default{
		name: `SearchCard`,
		data(){
			return{
				imgUrl: `../../../static/tick.jpg`,
				searchNumber: ``,
				upadteCard:false,
				deleteCard:false,
				//
				cardState:[``,``,``],
				UserCardTable:[],
				// 
				cardFormVisible: false,
				//
				isDisabled: false,
				//ID
				currentCardId: ``,
				//
				currentCardNum: ``,
				//
				modifyCardNum: ``,
			}
		},
		methods:{
			// 
			searchCard(){
				if(this.InputLimit(this.searchNumber))
				this.axios.post(this.baseURI + '/parkingCard/selectUserCardsForList', { "phone": this.searchNumber})
				.then( res => {
                    console.log(res);
                    this.UserCardTable = res.data.data;
				})
				.catch( err => {
					console.log(err);
				})
			},
			//
			handleCurrentChange(val) {
				this.currentCardNum = val.cardNum;
				this.currentCardId = val.id;
				console.log(this.currentCardNum + ` ` + this.currentCardId);
			},
            //
            secondToDate(date){
            	let res = new Date(date).toLocaleString();
            	return res.slice(0,res.indexOf(' '));
            },
            //
            InputLimit(cardNum){
                //
                	if(!cardNum){
                		this.$notify({
                			title: '',
                			message: '',
                			type: 'error'
                		});
                	}
                	else if(!(/^[0-9]*$/.test(cardNum))){
                		this.$notify({
                			title: '',
                			message: '',
                			type: 'error'
                		});
                	}else{ 
                		return true;
                	}
                    return false;
 			},
 			//
 			modifyCard(){
                 if(this.InputLimit(this.modifyCardNum))
                 	this.axios.post(this.baseURI + `/parkingCard/createNewCardReplaceOldOne`,{
                 		id: this.currentCardId,
                 		cardNum: this.currentCardNum
                 	})
                 .then( res =>{
                    console.log(res);
                 })
                 .err( err =>{

                 })
 			},
 			//
 			judgeChosenCard(){
 				if(this.currentCardNum){
 					this.cardFormVisible = true;
 				}
 				else{
 					this.$notify({
                			title: '',
                			message: '',
                			type: 'error'
                		});
 				}
 			}
		}
	}
</script>

<style scoped>
	.search-card{
		width: 980px;
	}
	.el-input {
		display: inline-block;
		width: 270px;
		margin-bottom: 30px;
	}
	.operator-card {
	  	margin-top: 30px;
  		text-align: right;
	}
	.el-form-item{
 		margin: 30px 0 10px 30px;
	}
	img{
		width: 20px;
		height: 20px;
	}
</style>


