<template>
	<div class="search-card">
		<el-input v-model.trim="searchNumber" placeholder=""></el-input>
		<el-button type="primary" @click="SearchUserBill(1)"></el-button>
    <!--  -->
		<el-table
          :data="UserBillData"
          style="width: 95%">
          <el-table-column
            label=""
            width="120"
            class="card-active">
            <template slot-scope="scope">
              <!--  -->
              <!-- <span v-if="scope.row.flag == 2">{{ scope.row.billNum }}</span> -->
              <!--  -->
              <router-link :to="{ name:'SearchBillDetail', params: {billNum: scope.row.billNum}}" class="card-active">{{ scope.row.billNum }} </router-link> 
            </template>
          </el-table-column>
          <el-table-column
            prop="cardNum"
            label=""
            width="125">
          </el-table-column>
          <el-table-column
            prop="price"
            label="/"
            width="120">
          </el-table-column>
          <el-table-column
            prop="account"
            label=""
            width="110">
          </el-table-column>
          <el-table-column
            prop="parkingName"
            label=""
            width="115">
          </el-table-column>
          <el-table-column
            label=""
            width="115">
             <template slot-scope="scope">
              <span>{{secondToDate(scope.row.statementDate)}}</span>
             </template>
          </el-table-column>
          <el-table-column
            label="">
            <template slot-scope="scope">
              <!--  -->
              <span v-if="scope.row.flag == 0 || scope.row.flag == 3" :class="{unpaid:scope.row.flag == 0 || scope.row.flag == 3}">{{ payArr[0] }}</span>
              <!--  -->
              <span v-else>{{ payArr[scope.row.flag] }}</span>
              <!--  -->
              <span v-if="scope.row.flag == 0 || scope.row.flag == 3"  class="tip">{{ '' + scope.row.tis + ''}}</span>
            </template>
          </el-table-column>
           <!-- <el-table-column>
            <template slot-scope="scope">
              <span class="tip">{{ scope.row.tip }}</span>
            </template>
          </el-table-column> -->
        </el-table>
	</div>
</template>

<script>
	export default{
		name: `SearchCard`,
		mounted(){
            this.$nextTick( () =>{
            	if(this.$route.params.phone){
                    //
                    if(this.$route.params.phone != 0)
                      this.time = this.$route.params.phone;
                    this.SearchUserBill();
            	}
            })
		},
		data(){
			return{
        //
        time: 0,
        // flag: ``,
        // 
				searchNumber: ``,
				//
				prePhone: ``,
				UserBillData:[],
				//
        		payArr:[``,``,``,``],
			}
		},
		methods:{
			//
			SearchUserBill(){
				//
				if(this.$route.params.phone == 0){
					this.prePhone = this.searchNumber;
        }
        //
        else
          this.prePhone = this.$route.params.phone;
        //
        if( this.InputLimit())
				this.axios.post(this.baseURI + '/parkingBill/selectAllParkingBillEntity', { "phone": this.prePhone})
                .then(res => {
                  // sessionStorage.getItem("phone")
                  console.log(res);
                  this.UserBillData = res.data.data;
                })
                .catch(err => {
                  console.log(err)
                })
			},
      //
      secondToDate(date){
        let res = new Date(date).toLocaleString();
        return res.slice(0,res.indexOf(' '));
      },
      InputLimit(){
      //
      if(this.time == 0){
        this.time++;
        return false;
      }
      //
      else{ 
        if(!this.prePhone){
          this.$notify({
            title: '',
            message: '',
            type: 'error'
          });
        }
        else if(!(/^[0-9]*$/.test(this.prePhone))){
          this.$notify({
            title: '',
            message: '',
            type: 'error'
          });
        }else{ 
          return true;
        }
      }
      return false;
    }

		}
	}
</script>

<style scoped>
	.search-card{
		width: 980px;
	}
	.el-input {
		display: inline-block;
		width: 270px;
		margin-bottom: 30px;
	}
</style>


