<template>
  <div class="admin-user">
    <!--  -->
    <el-table ref="multipleTable" :data="adminUserTable" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column label="" width="220">
        <template slot-scope="scope">{{ scope.row.username }}</template>
      </el-table-column>
      <el-table-column prop="usertype" label="" width="220">
      </el-table-column>
      <el-table-column label="" show-overflow-tooltip>
        <template slot-scope="scope">
          <a class="update-code" @click="upadteCodeDialog(scope.row.username,scope.row.id)"></a>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="admin-user-operate">
      <el-button type="primary" @click="addUserVisible = true"></el-button>
      <el-button type="primary" @click="delUserVisible=true"></el-button>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="delUserVisible" width="30%">
      <span></span>
      <span slot="footer" class="dialog-footer">
		    <el-button @click="delUserVisible = false"> </el-button>
		    <el-button type="primary" @click="delUser()"> </el-button>
			</span>
    </el-dialog>
    <!--  -->
    <el-dialog class="dialog-width" title="" :visible.sync="addUserVisible" width="30%">
      <el-input v-model.trim="adduser.username" type="text" /> 
      <el-input v-model.trim="adduser.password" type="password" />
      <el-radio-group v-model.trim="radio" @change="userTypeChoose">
        <el-radio :label="1"></el-radio>
        <el-radio :label="2"></el-radio>
      </el-radio-group>
      <div class="dialog-footer">
        <el-button @click="addUserVisible = false"> </el-button>
        <el-button type="primary" @click="addUser()"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="updateCodeVisible" width="30%">
      <form method="post">
        <el-input v-model.trim="updateCode.username" type="text" disabled/>
        <el-input v-model.trim="updateCode.code" type="password" /> 
        <el-input v-model.trim="updateCode.surecode" type="password" />
      </form>
      <div class="dialog-footer">
        <el-button @click="updateCodeVisible = false"> </el-button>
        <el-button type="primary" @click="upadteCode(updatecodeId)"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      radio: 1,
      updatecodeId: null,

      delUserVisible: false,
      addUserVisible: false,
      updateCodeVisible: false,

      multipleSelection: [],
      delUserId: [],
      usertype: ['', ''],
      adminUserTable: [],

      adduser: {
        username: '',
        password: '',
        usertype: 1,
      },

      updateCode: {
        username: '',
        code: '',
        surecode: ''
      }
    }
  },

  mounted: function() {
    this.$nextTick(function() {
      this.init();
    })
  },

  methods: {
    // 
    init() {
      this.axios.post(this.baseURI + '/admin/selectUserForList')
        .then(res => {
          this.adminUserTable = [];
          var data = res.data.data;
          for (var i = 0; i < data.length; i++) {
            if (data[i].role == 1) {
              data[i].usertype = this.usertype[0];
            } else {
              data[i].usertype = this.usertype[1];
            }
            this.adminUserTable.push(data[i]);
          }
          console.log(this.adminUserTable);
        })
        .catch(err => {
          console.log(err);
        })
    },
    // 
    userTypeChoose(value) {
      this.adduser.usertype = value;
    },
    // 
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    // 
    handleSelectionChange(val) {
      this.delUserId = [];
      this.multipleSelection = val;
      for (var i = 0; i < this.multipleSelection.length; i++) {
        this.delUserId.push(this.multipleSelection[i].id);
      }
    },
    // 
    addUser() {
      if (this.adduser.username == '' || this.adduser.password == '') {
        this.$notify({
          title: '',
          message: '',
          type: 'error'
        });
        return false
      } else {
        this.axios.post(this.baseURI + '/admin/insertAdminUser', { "username": this.adduser.username, "password": this.adduser.password, "role": this.adduser.usertype })
          .then(res => {
            console.log(res);
            var data = res.data;
            if (data.state == 1) {
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              });
              this.init();
            } else {
              this.$notify({
                title: '',
                message: data.message,
                type: 'error'
              });
            }
            this.addUserVisible = false;
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    // 
    delUser() {
      console.log(this.delUserId[0]);
      this.axios.post(this.baseURI + '/admin/deleteAdminUser', { "datas": this.delUserId })
        .then(res => {
          console.log(res);
          if (res.data.state == 1) {
            this.$notify({
              title: '',
              message: '',
              type: 'success'
            });
            this.init();
          } else {
            this.$notify({
              title: '',
              message: res.data.message,
              type: 'error'
            });
          }
          this.delUserVisible = false;
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 
    upadteCode(id) {
      if (this.updateCode.code == '' || this.updateCode.surecode == '') {
        this.$notify({
          title: '',
          message: '',
          type: 'success'
        });
        return false
      } else if (this.updateCode.code != this.updateCode.surecode) {
        this.$notify({
          title: '',
          message: '',
          type: 'error'
        });
        return false
      } else {
        this.axios.post(this.baseURI + '/admin/updatePasswordById', { "id": id, "password": this.updateCode.code })
          .then(res => {
            console.log(res);
            this.updateCodeVisible = false;
            if (res.data.state == 1) {
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              });
            } else {
              this.$notify({
                title: '',
                message: res.data.message,
                type: 'error'
              });
            }
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    // 
    upadteCodeDialog(username, id) {
      this.updateCode.username = username;
      this.updatecodeId = id;
      this.updateCodeVisible = true;
    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  text-decoration: none;
}

.admin-user {
  width: 980px;
  height: 90%;
}

.admin-user-search {
  float: right;
}

.admin-user-operate {
  margin-top: 30px;
  text-align: right;
}

.el-radio-group {
  width: 180px;
  margin-left: 20%;
  margin-top: 40px;
}

.el-table {
  margin-top: 20px;
}

.dialog-footer {
  width: 100%;
  margin-top: 30px;
  text-align: center;
}

.update-code {
  color: #f00;
  cursor: pointer;
}

</style>



